import React, { useState } from "react";
import "../Formulario/Style.css";
import axios from "axios";

const url = "https://erudito-dev.herokuapp.com/api/contactos";

export const FormularioApp = () => {
  const [nombre, setNombre] = useState([]);
  const [email, setEmail] = useState([]);
  const [telefono, setTelefono] = useState([]);
  const [mensaje, setMensaje] = useState([]);
  const [enviado, setEnviado] = useState(false);

  const guardarContacto = async (e) => {
    e.preventDefault();

    await axios.post(url, {
      nombre: nombre,
      email: email,
      telefono: telefono,
      mensaje: mensaje,
    });
    setEnviado(true);
    setTimeout(() => {
      setEnviado(null);
    }, 3000);
    setNombre("");
    setEmail("");
    setMensaje("");
    setTelefono("");
  };

  return (
    <div className="form">
      <p id="contactame">Contactame...</p>
      <div id="formulario">
        <form onSubmit={guardarContacto}>
          <div>
            <input
              value={nombre}
              onChange={(e) => setNombre(e.target.value)}
              className="form-control mt-2"
              type="texto"
              name="nombre"
              id="nombre"
              required
              placeholder="Nombre"
            />
          </div>

          <div>
            <input
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              className="form-control mt-2"
              type="email"
              name="nombre"
              id="nombre"
              required
              placeholder="Email"
            />
          </div>

          <div>
            <input
              value={telefono}
              onChange={(e) => setTelefono(e.target.value)}
              className="form-control mt-2"
              type="number"
              name="telefono"
              id="telefono"
              required
              placeholder="Telefono"
            />
          </div>

          <div>
            <textarea
              value={mensaje}
              onChange={(e) => setMensaje(e.target.value)}
              className="form-control mt-2"
              type="texto"
              name="mensaje"
              required
              id="mensaje"
              placeholder="Mensaje..."
            />
          </div>

          <div className="boton">
            <button 
            id="button"
            type="submit" className="btn btn-primary btn-sm mt-2 mb-2 ">
              Enviar
            </button>
            {enviado ? (
              <div className="alert alert-info mt-2" id="alerta">
                !Mensaje Enviado
              </div>
            ) : null}
          </div>
        </form>
      </div>
    </div>
  );
};
