import React from "react";

export const Informacion = () => {
  return (
    <>
      <div className="informacion">
        <h3 id="titulo_0">Desarrollador Frond-End</h3>
        <p id="texto_0">
          "Frontend es la parte de un programa o dispositivo a la que un usuario
          puede acceder directamente. Son todas las tecnologas de diseo y
          desarrollo web que corren en el navegador y que se encargan de la
          interactividad con los usuarios. Los desarrolladores frontend son
          aquellos que crean los componentes visuales de un sitio web. Dan
          formato a las imgenes, la animacin y la interactividad de un sitio
          web
        </p>

        <h3 id="titulo_1">Desarrollador Back-End</h3>
        <p id="texto_1">
          "Backend es la capa de acceso a datos de un software o cualquier
          dispositivo, que no es directamente accesible por los usuarios.
          Adems, contiene la lgica de la aplicacin que maneja dichos datos.
          El Backend tambin accede al servidor, que es una aplicacin
          especializada que entiende la forma en la que el navegador hace
          solicitudes. Los desarrolladores backend se encargan de hacer que la
          lgica del sitio funcione correctamente, la informacin se transmita
          de manera segura y el desempeo de la aplicacin no entorpezca la
          experiencia del usuario.
        </p>
      </div>
    </>
  );
};
